.cart-page {
    margin-top: 64px;
  }
  
  .cart-page h1 {
    padding: 15px;
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    background-color: rgba(0, 0, 255, 0.072);
  }
  
  .cart-page h1 > p {
    font-size: 20px;
    margin-top: 10px;
  }
  
  .cart-page .card {
    padding: 5px;
    height: 150px;
    margin-bottom: 5px;
  }
  
  .cart-page .cart-remove-btn {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .cart-page .cart-summary {
    text-align: center;
    /* margin-top: -140px; */
  }
  
  @media (max-width: 768px) {
    .cart-page .cart-summary {
      margin-top: 20px;
    }
  }
  